import React, { Component } from "react";
import Carousel from "react-bootstrap/Carousel";
import natureImg from "../Images/nature-forest-trees-fog-4827.jpg";
import seashoreImg from "../Images/seashore-and-mountain-3067769.jpg";
import greenTreesImg from "../Images/worms-eyeview-of-green-trees-957024.jpg";

export default class Header extends Component {
  render() {
    return (
      <Carousel>
        <Carousel.Item>
          <img
            className="d-block w-100 bg-img"
            src={natureImg}
            alt="Nature forest"
          />
          <Carousel.Caption>
            <h3>Forest Image</h3>
            <p>Lorem ipsum dolor aset mit night glosios mine ambassor</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img className="d-block w-100 bg-img" src={seashoreImg} alt="Sea" />
          <Carousel.Caption>
            <h3>Sea Image</h3>
            <p>Lorem ipsum dolor aset mit night glosios mine ambassor</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 bg-img"
            src={greenTreesImg}
            alt="Green forest"
          />
          <Carousel.Caption>
            <h3>Green forest Image</h3>
            <p>Lorem ipsum dolor aset mit night glosios mine ambassor</p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }
}
